
.lottery {
  background: url("@/assets/lottery/bg.png") no-repeat;
  background-size: 100% 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  .contentBox {
    display: flex;
    justify-content: center;
    .content {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      .content-top {
        width: 90%;
        display: flex;
        justify-content: flex-end;
        .address {
          width: 0.48rem;
          height: 0.45rem;
          background: #ff2a00;
          border-radius: 0px 0px 30px 30px;
          display: flex;
          flex-direction: column;
          align-items: center;
          .receive {
            font-size: 13px;
            font-family: PingFang SC-中等, PingFang SC;
            font-weight: normal;
            color: #fefffe;
            line-height: 18px;
            margin: 2px 0 0;
          }
          .site {
            font-size: 13px;
            font-family: PingFang SC-中等, PingFang SC;
            font-weight: normal;
            color: #fefffe;
            line-height: 18px;
          }
        }
      }
      .content-center {
        width: 100%;
        display: flex;
        justify-content: center;
        .sign {
          width: 2.77rem;
          height: 1.11rem;
          display: flex;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
  .introduceBox {
    display: flex;
    justify-content: center;
    .introduce {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 24px 0 0;
      .introduce-top {
        display: flex;
        justify-content: center;
        .label {
          width: 2.38rem;
          height: 0.4rem;
          background: url("@/assets/lottery/label.png") no-repeat;
          background-size: 100% 100%;
          display: flex;
          justify-content: center;
          .title {
            font-size: 20px;
            font-family: PingFang SC-粗体, PingFang SC;
            font-weight: normal;
            color: #ffffff;
            line-height: 20px;
            margin: 6px 0 0;
          }
        }
      }
      .introduce-center {
        width: 100%;
        display: flex;
        justify-content: center;
        .activity {
          width: 90%;
          display: flex;
          justify-content: center;
          background: #6807a2;
          border-radius: 16px;
          border: 1px solid #ddb6f4;
          margin: 13px 0 35px;
          .information {
            width: 90%;
            display: flex;
            flex-direction: column;
            margin: 19px 0 20px;
            .name,
            .time,
            .join_count,
            .award_count {
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #fefffe;
              line-height: 26px;
            }
            .rule {
              display: flex;
              .message {
                font-size: 14px;
                font-family: PingFang SC-中等, PingFang SC;
                font-weight: normal;
                color: #fefffe;
                line-height: 26px;
              }
            }
          }
        }
      }
    }
  }
  .remind_dialog {
    display: flex;
    .remind {
      display: flex;
      flex-direction: column;
      align-items: center;
      .title {
        font-size: 20px;
        font-family: PingFang SC-粗体, PingFang SC;
        font-weight: normal;
        color: #ff2a00;
        line-height: 20px;
        margin: 43px 0 0;
      }
      .message {
        font-size: 16px;
        font-family: PingFang SC-中等, PingFang SC;
        font-weight: normal;
        color: #333333;
        line-height: 18px;
        margin: 30px 0;
      }
      .confirm {
        width: 1.62rem;
        height: 0.48rem;
        background: linear-gradient(180deg, #ff5736 0%, #ff2a00 100%);
        border-radius: 24px;
        font-size: 16px;
        font-family: PingFang SC-中等, PingFang SC;
        font-weight: normal;
        color: #ffffff;
        line-height: 16px;
        margin: 0 0 44px;
      }
    }
  }
}
